  .footer__item:nth-child(n+3) {
    margin: -44.5px 0 0 10px;
    padding: 5px 0 7px;
  }
}

.footer__link {
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  padding: 2px;
  cursor: pointer;
  user-select: none;
}
.footer__link:hover, .footer__link:focus, .footer__link:active {
  text-shadow: 0.1px 0.1px 5px #ffffff;
}
@media (max-width: 767px) {
  .footer__link {
    padding: 2px;
    margin: 8px -6px 4px -2px;
    font-size: 16px;
  }
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  gap: 11px;
}
@media (max-width: 1199px) {
  .footer__social-list {
    gap: 6px;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    grid-column: 1/-1;
    grid-row: 3;
    gap: 11px;
  }
}

.footer__social-link {
  position: relative;
  background-color: #ffffff;
  border-radius: 50%;
  height: 25px;
  width: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  cursor: pointer;
}
.footer__social-link:hover, .footer__social-link:focus, .footer__social-link:active {
  box-shadow: 0.1px 0.1px 5px #ffffff;
}

.footer__icon {
  fill: #030a1a;
}

